.theme-switcher {
  background-image: linear-gradient(to bottom, var(--color-violet), var(--color-white) );
  border-radius: 18px;
  display: inline-block;
  position: relative;
}

.switch__input {
  width: 0;
  height: 0;
  opacity: 0;
  appearance: none;
  position: absolute;
}

.switch__slider {
  display: inline-block;
  position: relative;
}

.switch__slider:before {
  content: "";
  background-image: linear-gradient(to bottom, var(--color-white), #f7f7f7);
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: left .3s;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switch__slider:after {
  content: "";
  width: 40px;
  height: 20px;
  background-image: linear-gradient(to bottom, var(--color-violet), var(--color-white) );
  border-radius: 18px;
  display: block;
}

.switch__input:checked + .switch__slider:before {
  content: "";
  display: block;
  position: absolute;
  top: 1px;
  left: 21px;
}

.switch__input:checked + .switch__slider:after {
  background-image: linear-gradient(to bottom, var(--color-violet), #686868);
}

